.li {
  margin: 10px 0;
  display: flex;
  align-items: center;
  flex: 1 1 auto;
}

.li button {
  width: 20%;
  background-color: transparent;
  height: 48px;
  background-color: #e75a28;
  border: solid 2px #e75a28;
  border-radius: 5px;
  color: #f1f3f6;
  transition: linear 0.7s;
}

.li button:hover {
  cursor: pointer;
  color: #e75a28;
  background-color: transparent;
}

.name {
  flex-grow: 2;
}

