@media all and (max-width: 726px) {
  .li button {
    width: 80px;
  }
}

@media all and (max-width: 480px) {
  .icon {
    display: none;
  }
}

@media all and (max-width: 405px) {
  .li {
    display: block;
    margin: 20px 0;
  }

  .li button {
    margin-top: 10px;
    width: 100%;
    height: 30px;
  }
}